import React from 'react';
import './header.scss';

function Name(props) {
  return <div className='header-name'>{props.name}</div>;
}

function Hr(props) {
  return <hr />;
}

function Links(props) {
  const {links} = props;
  const listItems = links.map((link) =>
    <li className='header-links'><a href='#'>{link}</a></li>
  );

  return (
    <ul className='header-links-container'>{listItems}</ul>
  )
}

const links = ['Home', 'Resume', 'Portfolio', 'Media'];
function Header() {
  return (
    [<Name name='Stephanie Nathai-Cueter '/>,
     <Hr />,
     <Links links={links} />,
    ]
  )
}

export default Header;
